.l-constrained {
	@include outer-container;

	width: 1140px;
}

.l-page {
	background: $off-white;
}

.l-region--navigation {
	@extend .l-constrained;
}

.l-header .l-constrained {}

header.l-header {
	background: white;
}

.site-branding {
	@include span-columns(9);

	//@include outer-container;
	display:flex;

	.site-branding__logo {
		//@include span-columns(2);
		width: 165px;
		display : inline-block;
		margin-right: 10px;
		line-height: 0;
	}

	.name_slogan {
		//@include span-columns(9);

		border-left: $base-border;
		padding-left: 10px;
		padding-top: 4px;

		a {
			margin: 0;
		}

		//@include omega;

	}

	@include clearfix;
}

.branding-container {
	padding: ($base-spacing / 2) 0;
	/*display: flex;*/
}

.l-region--header {
	@include span-columns(3);

	@include omega;
}

.site-branding {}

/* Content Area */
.l-highlighted-wrapper {}

.l-region--highlighted {
	@extend .l-constrained;
}

.l-region--help {
	margin-bottom: $base-spacing;
}

.l-main {
	padding: $base-spacing 0;
}

.has-one-sidebar.has-sidebar-first {
	.l-region--sidebar-first {
		/*background: red;*/

		@include span-columns(3);
	}

	.l-content {
		/*background: green;*/
		@include span-columns(9);

		@include omega;
	}
}

.has-one-sidebar.has-sidebar-second {
	.l-region--sidebar-second {
		/*background: red;*/

		@include span-columns(3);

		@include omega;
	}

	.l-content {
		/*background: green;*/
		@include span-columns(9);
	}
}

